export function colIdx2colName(colIdx) {
  /**
   * Convert col index to col name.
   * 
   * @param {int}     colIdx (1, 2, 3, ...)
   * 
   * @return {String} colName (A, B, C, ...)
   */
  let dividend = Math.floor(Math.abs(colIdx)), rest, colName = '';
  while (dividend > 0) {
    rest = (dividend - 1) % 26;
    colName = String.fromCharCode(65 + rest) + colName;
    dividend = parseInt((dividend - rest) / 26);
  }
  return colName;
}

export function colName2colIdx(colName) {
  /**
   * Convert col name to col index.
   * 
   * @param {String}  colName (A, B, C, ...)
   * 
   * @return {int}    colIdx (1, 2, 3, ...)
   */
  const chrs = ' ABCDEFGHIJKLMNOPQRSTUVWXYZ';
  let colIdx = 0;
  for (let i = 0; i < colName.length; i++) {
    colIdx = colIdx * 26 + chrs.indexOf(colName[i]);
  }
  return colIdx;
}

export function getHueByRandom(total) {
  /**
   * Randomly generate different colors.
   * 
   * @param {int}     total total number of colors
   * 
   * @return {int}    hue, number in [0, 360)
   */
  const division = Math.floor(360 / total);
  return Math.floor(Math.random() * total) * division;
}

export function getHueByQNode(total, qnode) {
  /**
   * Use qnode as random seed to generate different colors.
   * 
   * @param {int}     total total number of colors
   * @param {String}  qnode
   * 
   * @return {int}    hue, number in [0, 360)
   */
  const i = parseInt(qnode.substring(1)) % total;
  const division = Math.floor(360 / total);
  return i * division;
}

export function getRegions(cells) {
  /**
   * Infer region array from given cell array
   * 
   * @param {Array}   cells e.g. ["A1", "A2", "B1", "B2", ...]
   * 
   * @return {Array}  regions e.g. ["A1:B2", ...]
   */
  let len = cells.length;

  // convert cells (A1) to cols & rows (1 & 1)
  let cols = new Array(len), rows = new Array(len);
  for (let i = 0; i < len; i++) {
    let [col, row] = cells[i].match(/[a-z]+|[^a-z]+/gi);
    cols[i] = colName2colIdx(col);
    rows[i] = parseInt(row);
  }

  // init bool table
  let colLen = Math.max(...cols) + 1, rowLen = Math.max(...rows) + 1;
  let table = new Array(rowLen).fill(undefined).map(() => new Array(colLen).fill(false));
  for (let i = 0; i < len; i++) {
    table[rows[i]][cols[i]] = true;
  }

  // find regions
  let regions = [];
  while (true) {
    // find first cell
    let [left, top] = findFirstCell(table, rowLen, colLen);
    if (left === null) break;

    // determine whether go down or go right, and max region
    let right = left, bottom = top;
    while (bottom + 1 < rowLen && table[bottom + 1][left]) bottom += 1;
    while (right + 1 < colLen && table[top][right + 1]) right += 1;
    if (bottom - top >= right - top) {
      // fix top/bottom/left, go right
      right = left;
      while (right + 1 < colLen && canRegionGoRight(table, top, bottom, right + 1)) right += 1;
    } else {
      // fix left/right/top, go down
      bottom = top;
      while (bottom + 1 < rowLen && canRegionGoDown(table, left, right, bottom + 1)) bottom += 1;
    }

    // clear region
    clearRegion(table, left, top, right, bottom);

    // output region
    regions.push(getRegionStr(left, top, right, bottom));
  }
  return regions;
}

function findFirstCell(table, rowLen, colLen) {
  /**
   * In bool table, find first true cell.
   * 
   * @param {Array} table
   * @param {int}   rowLen number of rows
   * @param {int}   colLen number of cols
   * 
   * @return {int, int} [row, col]
   */
  for (let i = 0; i < rowLen; i++) {
    for (let j = 0; j < colLen; j++) {
      if (table[i][j]) return [j, i];
    }
  }
  return [null, null];
}

function canRegionGoRight(table, top, bottom, right) {
  /**
   * In bool table, check all cells are true in col (right) from (top) to (bottom).
   * 
   * @param {Array} table
   * @param {int}   top
   * @param {int}   bottom
   * @param {int}   right
   * 
   * @return {bool} is all cells are true
   */
  for (let row = top; row <= bottom; row++) {
    if (!table[row][right]) return false;
  }
  return true;
}

function canRegionGoDown(table, left, right, bottom) {
  /**
   * In bool table, check all cells are true in row (bottom) from (left) to (right).
   * 
   * @param {Array} table
   * @param {int}   left
   * @param {int}   right
   * @param {int}   bottom
   * 
   * @return {bool} is all cells are true
   */
  for (let col = left; col <= right; col++) {
    if (!table[bottom][col]) return false;
  }
  return true;
}

function clearRegion(table, left, top, right, bottom) {
  /**
   * Make all cells in region of table become false.
   * 
   * @param {Array} table
   * @param {int} left
   * @param {int} top
   * @param {int} right
   * @param {int} bottom
   */
  for (let i = top; i <= bottom; i++) {
    for (let j = left; j <= right; j++) {
      table[i][j] = false;
    }
  }
}

function getRegionStr(left, top, right, bottom) {
  /**
   * Convert region (A, 1, B, 2) to string (A1:B2).
   * 
   * @param {int} left    e.g. 1 -> A
   * @param {int} top     e.g. 1
   * @param {int} right   e.g. 2 -> B
   * @param {int} bottom  e.g. 2
   * 
   * @return {String} e.g. "A1:B2"
   */
  return colIdx2colName(left) + top + ":" + colIdx2colName(right) + bottom;
}

export function region2cells(region) {
  /**
   * Convert region (A1:B2) to cell array [A1, A2, B1, B2].
   * 
   * @param {String} region e.g. "A1:B2"
   * 
   * @return {Array} cell array (col first order) e.g. ["A1", "A2", "B1", "B2"]
   */
  let [left, top, right, bottom] = region.match(/[a-z]+|\d+/gi);
  left = colName2colIdx(left);
  right = colName2colIdx(right);
  top = parseInt(top);
  bottom = parseInt(bottom);

  let cells = new Array((right - left + 1) * (bottom - top + 1));
  let i = 0;
  for (let col = left; col <= right; col++) {
    for (let row = top; row <= bottom; row++) {
      cells[i] = colIdx2colName(col) + row;
      i++;
    }
  }
  return cells;
}

export function isValidRegion(region) {
  /**
   * Check whether region (A1:B2) is valid.
   * 
   * @param {String}    region e.g. "A1:B2"
   * 
   * @return {boolean}  true if the region is valid
   */
  let [left, top, right, bottom] = region.match(/[a-z]+|\d+/gi);
  left = colName2colIdx(left);
  right = colName2colIdx(right);
  top = parseInt(top);
  bottom = parseInt(bottom);
  return (left <= right) && (top <= bottom);
}

export function sortCells(cells) {
  /**
   * Sort cell array in col first order.
   * 
   * @param {Array}   cells e.g. [A1, B2, B1, A2]
   * 
   * @return {Array}  orderedCells e.g. [A1, A2, B1, B2]
   */
  const len = cells.length;
  let cellArray = new Array(len);
  for (let i = 0; i < len; i++) {
    let [col, row] = cells[i].match(/[a-z]+|[^a-z]+/gi);
    col = colName2colIdx(col);
    row = parseInt(row);
    cellArray[i] = [col, row];
  }
  cellArray.sort(function (cell1, cell2) {
    if (cell1[0] !== cell2[0]) return cell1[0] - cell2[0];
    else return cell1[1] - cell2[1];
  });
  let orderedCells = new Array(len);
  for (let i = 0; i < len; i++) {
    let [col, row] = cellArray[i];
    orderedCells[i] = colIdx2colName(col) + row;
  }
  return orderedCells;
}